.TodoSearch {
    background: #c87bc6;
    border-radius: 2px;
    border: 2px solid #9b0785;
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 100px);
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  }
  
  .TodoSearch::placeholder {
    color: #fbf9fbeb;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  .TodoSearch:focus {
    outline-color: #14c5cb;
  }