.LoadingTodo-container {

    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0 5px 50px rgba(32, 35, 41, .15)
}

.LoadingTodo-text {
    margin: 24px 0 24px 24px;
    width: calc(100%-120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon {

    width: 48px;
    height: 48px;
    border-radius: 50px;

}
.LoadingTodo-completeIcon {
  position: absolute;
    left:12px;
}

.LoadingTodo-delete {
    position: absolute;
      top:-24px;
      right:0;
  }

  .LoadingTodo-container,
.LoadingTodo-completeIcon,
.LoadingTodo-completeIcon {

   background: linear-gradient(90deg,rgb(163, 157, 157),rgba(200,199,199));
   background-size: 400% 400%;
   animation: loadingAnimation 3s ease-in-out infinite;

}

@keyframes loadingAnimation{
    0%{
        background-position: 0% 50%;
    }
    50%{
        background-position: 100% 50%;
    }
    100%{
        background-position: 0% 50%;
    }
}